@import 'sass';

.editGoals {
	margin-top: 10px;
	max-width: 1200px;
	position: relative;
	left: 50%;
	transform: translateX(-50%);
	display: flex;
	justify-content: center;

	@media screen and (max-width: $breakpoint-tablet) {
		flex-direction: column;
		align-items: center;
	}

	.leftColumn {
		width: 360px;
		// flex-shrink: 0;
		display: flex;
		flex-direction: column;
		align-items: center;

		@media screen and (max-width: $breakpoint-tablet) {
			width: 100%;
			flex-direction: row;
			align-items: flex-start;
		}

		@media screen and (max-width: 600px) {
			width: 100%;
			flex-direction: column;
		}
	}

	.goalsDisplay {
		position: relative;
		flex-shrink: 2;
		width: 70%;

		@media screen and (max-width: $breakpoint-tablet) {
			width: 90%;
		}

		.roundedModal {
			border-radius: 15px;
		}

		.header {
			text-transform: capitalize;
			align-self: flex-start;
			margin-left: 10px;
		}
		// --> custom container colors:
		.container {
			@include container;
			.icons {
				display: flex;
				justify-content: center;
				align-items: center;
			}
			&0 {
				@extend .container;
				border-left: 2px solid $theme-color-default;
				li {
					border: 1.5px solid $theme-color-default;
				}
			}
			&1 {
				@extend .container;
				border-left: 2px solid $theme-color-one;
				li {
					border: 1.5px solid $theme-color-one;
				}
			}
			&2 {
				@extend .container;
				border-left: 2px solid $theme-color-two;
				li {
					border: 1.5px solid $theme-color-two;
				}
			}
			&3 {
				@extend .container;
				border-left: 2px solid $theme-color-three;
				li {
					border: 1.5px solid $theme-color-three;
				}
			}
			&4 {
				@extend .container;
				border-left: 2px solid $theme-color-four;
				li {
					border: 1.5px solid $theme-color-four;
				}
			}
			&5 {
				@extend .container;
				border-left: 2px solid $theme-color-five;
				li {
					border: 1.5px solid $theme-color-five;
				}
			}
			&6 {
				@extend .container;
				border-left: 2px solid $theme-color-six;
				li {
					border: 1.5px solid $theme-color-six;
				}
			}
			&7 {
				@extend .container;
				border-left: 2px solid $theme-color-seven;
				li {
					border: 1.5px solid $theme-color-seven;
				}
			}
			&8 {
				@extend .container;
				border-left: 2px solid $theme-color-eight;
				li {
					border: 1.5px solid $theme-color-eight;
				}
			}
		}
	}
}
